import datas from '../../commons/js/data.js';
import myfun from '../../commons/js/myfun.js';
export default {
	data() {
		return {
			friends: [],
			uid: '',
			imgurl: '',
			token: '',
			myname: '',
			requestData: 0, //申请数
			requestTime: '', //最后申请时间
			refresh: false,
			noone:false, // 是否没有好友
		};
	},
	onLoad() {
		this.getStorages();
		this.getFriends();
		this.friendRequest();
		this.join(this.uid);
		this.receiveSocketMsg();
	},
	onPullDownRefresh() {
	        this.friends = [];
	        this.getStorages();
	        this.getFriends();
	        this.friendRequest();
	        setTimeout(function () {
	            uni.stopPullDownRefresh();
	        }, 1000);
	},
	methods: {
		changeTime: function(date) {
			return myfun.dateTime(date);
		},
		// 获取缓存数据
		getStorages() {
			try {
				const value = uni.getStorageSync('user');
				if (value) {
					this.uid = value.id;
					this.imgurl = this.serverUrl + value.imgurl;
					this.token = value.token;
					this.myname = value.name;
				} else {
					uni.navigateTo({
						url: '../signin/signin'
					});
				}
			} catch (e) {}
		},
		// 获取好友列表
		getFriends: function() {
			uni.request({
				url: this.serverUrl + '/index/getfriend',
				data: {
					uid: this.uid,
					state: 0,
					token: this.token
				},
				method: 'POST',
				success: data => {
					this.refresh = true;
					let status = data.data.status;
					console.log(data.data)
					if (status == 200) {
						let res = data.data.result;
						if (res.length > 0) {
							this.noone = true;
							for (let i = 0; i < res.length; i++) {
								res[i].imgurl = this.serverUrl + res[i].imgurl;
								if (res[i].markname) {
									res[i].name = res[i].markname;
								}
								this.friends.push(res[i]);
							}
							this.friends = myfun.sort(this.friends, 'lastTime', 0);
						    // 获取好友内信息
						    for (let i = 0; i < this.friends.length; i++) {
							    this.getLastMsg(this.friends, i);
							    this.getUnread(this.friends, i);
						    }
						}else{
							this.noone = false;
						}
						// this.getGroup();
					} else if (status == 300) {
						uni.navigateTo({
							url: '../signin/signin?name=' + this.myname
						});
					} else if (status == 500) {
						uni.showToast({
							title: '服务器出错了！',
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		// 获取群列表
		getGroup: function() {
			uni.request({
				url: this.serverUrl + '/index/getgroup',
				data: {
					uid: this.uid,
					token: this.token
				},
				method: 'POST',
				success: data => {
					let status = data.data.status;
					if (status == 200) {
						let res = data.data.result;
						if (res.length > 0) {
							for (let i = 0; i < res.length; i++) {
								res[i].imgurl = this.serverUrl + res[i].imgurl;
								this.friends.push(res[i]);
							}
						}
						this.friends = myfun.sort(this.friends, 'lastTime', 0);
						// 获取好友内信息
						for (let i = 0; i < this.friends.length; i++) {
							this.getLastMsg(this.friends, i);
						}
					} else if (status == 300) {
						uni.navigateTo({
							url: '../signin/signin?name=' + this.myname
						});
					} else if (status == 500) {
						uni.showToast({
							title: '服务器出错了！',
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		// 好友申请
		friendRequest() {
			uni.request({
				url: this.serverUrl + '/index/getfriend',
				data: {
					uid: this.uid,
					state: 1,
					token: this.token
				},
				method: 'POST',
				success: data => {
					this.refresh = true;
					let status = data.data.status;
					if (status == 200) {
						this.noone = true;
						let res = data.data.result;
						this.requestData = res.length;
						if (res.length > 0) {
							this.requestTime = res[0].lastTime;
							for (let i = 0; i < res.length; i++) {
								if (this.requestTime < res[i].lastTime) {
									this.requestTime = res[i].lastTime;
								}
							}
						}else{
							this.noone = false
						}
					} else if (status == 500) {
						uni.showToast({
							title: '服务器出错了！',
							icon: 'none',
							duration: 2000
						});
					} else if (status == 300) {
						uni.navigateTo({
							url: '../signin/signin?name=' + this.myname
						});
					}
				}
			});
		},
		// 获取最后消息
		getLastMsg(arr, i) {
			uni.request({
				url: this.serverUrl + '/index/getlastmsg',
				data: {
					uid: this.uid,
					fid: arr[i].id,
					token: this.token
				},
				method: 'POST',
				success: data => {
					let status = data.data.status;
					// console.log(data.data)
					if (status == 200) {
						let res = data.data.result;
						if (res.types == 0) {
							// 文字
						} else if (res.types == 1) {
							// 图片
							res.message = '[图片]';
						} else if (res.types == 2) {
							// 音频
							res.message = '[语音]';
						} else if (res.types == 3) {
							// 定位
							res.message = '[位置]';
						}
						let e = arr[i];
						e.msg = res.message;
						arr.splice(i, 1, e);
					} else if (status == 300) {
						uni.navigateTo({
							url: '../signin/signin?name=' + this.myname
						});
					} else if (status == 500) {
						uni.showToast({
							title: '服务器出错了！',
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		// 获取未读消息数
		getUnread(arr, i) {
			uni.request({
				url: this.serverUrl + '/index/unreadmsg',
				data: {
					uid: this.uid,
					fid: arr[i].id,
					token: this.token
				},
				method: 'POST',
				success: data => {
					let status = data.data.status;
					// console.log(data.data)
					if (status == 200) {
						let res = data.data.result;
						let e = arr[i];
						e.tip = res;
						arr.splice(i, 1, e);
					} else if (status == 300) {
						uni.navigateTo({
							url: '../signin/signin?name=' + this.myname
						});
					} else if (status == 500) {
						uni.showToast({
							title: '服务器出错了！',
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		goRequest() {
			uni.navigateTo({
				url: '../friendrequest/friendrequest'
			});
		},
		// 跳转到聊天页面
		toChatRoom(data) {
			console.log(data)
			uni.navigateTo({
				url: '../chatroom/chatroom?id=' + data.id + '&name=' + data.name + '&imgurl=' + data.imgurl + '&type=' + data.type
			});
		},
		// 跳转到搜索页面
		toSearch: function() {
			uni.navigateTo({
				url: '../search/search'
			});
		},
		//socket 模块
		//用户登录socket注册
		join: function(uid) {
			this.socket.emit('login', uid);
		},
		// socket聊天数据接收
		receiveSocketMsg(){
			this.socket.on('msg',(msg,fromid) => {
				let nmsg = '';
				if(msg.types == 0){
					nmsg = msg.message
				}else if(msg.types == 1){
					nmsg = '[图片]'
				}else if(msg.types == 2){
					nmsg = '[音频]'
				}else{
					nmsg = '[位置]'
				}
				for(let i = 0; i < this.friends.length; i++){
					if(this.friends[i].id == fromid){
						let e = this.friends[i];
						e.lastTime = new Date();
						e.msg = nmsg;
						e.tip++;
						// 删除原来数据项
						this.friends.splice(i,1,e)
						// 新消息插入到最顶部
						this.friends.unshift(e)
					}
				}
			})
		}
	}
};
